import Data from "@/js/core/data";
const Feedback_Server = "https://api.${region}.500apps.com/support/feedback";

class Feedback extends Data {
  constructor() {
    super(Feedback_Server);
    this.disablePCors();
  }
  async add_feedback(form, domain_id, group_id, ticket_id) {
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = `${Feedback_Server}/${domain_id}/${group_id}/${ticket_id}`;
    this.url = eval("`" + url + "`");
    return await super.multi_call(form);
  }
}
export default new Feedback();
