<template
  ><div>
    <b-row>
      <b-col class="md-4"> </b-col>
      <b-col class="md-4" md="4">
        <b-card class="mt-8" no-body>
          <b-card-body>
            <div v-if="show_success_message" class="text-center py-4">
              <b-img src="/img/support/success-img.gif" width="60"></b-img>
              <h1 class="mt-5 mb-2">Submitted</h1>
              <p class="mb-1">Your feedback submitted successfully.</p>
              <p class="mb-1">Thank you for your time and patience.</p>
            </div>
            <div v-if="!show_success_message" class="py-2">
              <h2 class="text-center mb-3">How do you rate this service?</h2>
              <div class="text-center mb-3">
                <div class="mx-auto h2 mt-3">
                  <span :class="['fe fe-thumbs-up cursor-pointer', form.rating == '5' ? 'text-primary' : '']" @click="like()"></span>
                  <span :class="['fe fe-thumbs-down ml-3 cursor-pointer', form.rating == '1' ? 'text-primary' : '']" @click="disLike()"></span>
                </div>
              </div>
              <b-form ref="form" class="pt-2">
                <b-col sm="11" md="11" class="mx-auto">
                  <b-form-group label-for="comments">
                    <b-form-textarea rows="3" v-model="form.comment" id="comments" placeholder="Comment" maxlength="1000"></b-form-textarea>
                    <span class="mb-0 text-muted small-2 float-right">{{ form.comment.length }}/1000</span>
                  </b-form-group>
                </b-col>
                <b-button variant="primary" size="sm" class="mx-auto d-block px-5" @click="saveFeedback()">Submit <b-spinner v-if="show_loader" small label="Spinning smalll"></b-spinner></b-button>
              </b-form>
            </div>
          </b-card-body>
        </b-card>
      </b-col>
      <b-col class="md-4"> </b-col>
    </b-row>
  </div>
</template>
<script>
  import Feedback from "@/js/support/feedback/feedback.js";
  export default {
    data() {
      return {
        form: {
          comment: "",
          rating: "",
        },
        show_success_message: false,
        show_loader: false,
      };
    },
    async mounted() {
      this.form.rating = this.$route.query.rating;
    },
    methods: {
      // Save feedback
      async saveFeedback() {
        this.show_loader = true;
        if (!this.form.comment) delete this.form["comment"];
        this.form.rating = this.form.rating == 1 ? 0 : 5;
        this.form.created_by = window.user;
        let response = await Feedback.add_feedback(this.form, window.domain, this.$route.params.group_id, this.$route.params.ticket_id);
        if (response.GENERATED_KEY) {
          this.show_loader = false;
          this.show_success_message = true;
        }
      },
      like() {
        this.$router.push({
          query: { rating: "5" },
        });
        this.form.rating = 5;
      },
      disLike() {
        this.$router.push({
          query: { rating: "1" },
        });
        this.form.rating = 1;
      },
    },
  };
</script>
<style scoped>
  .cursor-pointer {
    cursor: pointer;
  }
</style>
